
.joinup-head{
    padding: 20px 15px;
    padding-left: 130px;
}
.joinup-head>div {
	    display: inline-block;
	    vertical-align: middle;
}
.head-left-text{
		display: inline-block;
		vertical-align: middle;
		text-align: left;
}
.joinup-head-left{
		width: 40%;
		text-align: right;
}
.joinup-head-right{
		width: 60%;
		text-align: right;
		color: #d43c33;
		font-size: 1.8vw;
}
.joinup-head-left img{
		width: auto;
    height: 50px;
    vertical-align: middle;
    margin-right: 10px;
    border-radius: 25px;
}
.head-left-text>p span:nth-child(1){
		font-size: 2vw;
    font-weight: bold;
}
.head-left-text>p span:nth-child(2){
		margin: 0 5px;
    font-weight: bold;
}
.head-left-text>p span:nth-child(3){
		display: inline-block;
    background: #e9f4de;
    color: #4e9900;
    font-size: 1.5vw;
    padding: 2px 5px;
}
.joinup-conent{
		height: 500px;
    position: relative;
}
.joinup-conent-left {
		position: absolute;
    height: 500px;
		width: 20%;
    text-align: center;
    background: rgba(48,132,218,0.7);
    color: #fff;
    padding: 0px 15px 0px 15px;
}
.joinup-conent-right{
		    height: 100%;
}
.joinup-conent-right img{
		height: 500px;
    width: 100%;
}
.joinup-content-top{
	  border-bottom: 1px solid #fff;
		padding-bottom: 10px;
		margin-top: 40%;
}
.joinup-content-top img{
		width: auto;
    height: 50px;
    margin-bottom: 10px;
    border-radius: 25px;
}
.joinup-content-bottom{
		margin-top: 10px;
}
.joinup .num {
		font-size: 5vw;
}
.joinup .choose-img{
		display: inline-block;
	    padding: 10px 20px;
	    border: 1px solid #3c9bff;
	    color: #3c9bff;
	    border-radius: 5px;
	    margin-left: 10px;
	    position: relative;
	    font-size: 1vw;
	    vertical-align: middle;
}
.joinup .choose-img img{
		width: auto;
		height: 25px;
}
.joinup .choose-img span{
		display: block;
}
.joinup .patients-img{
		padding:10px 0;
}
.joinup .choose{
		position: absolute;
		width: 100%;
		height: 100%;
		top: 0;
		left:0;
		opacity: 0;
}
.joinup-bottom>div{
    display: inline-block;
    vertical-align: middle;
}
.joinup-bottom-left{
		width: 10%;
    text-align: center;
}
.joinup-bottom-center{
		width: 30%;
}
.joinup-bottom-center textarea{
		width: 100%;
    height: 65px;
}
.joinup-bottom-right{
    margin-left: 10px;
}
.joinup-bottom{
		padding: 10px 0;
}
.joinup-report{
		width: 65px;
    height: 65px;
    vertical-align: middle;
    margin-left: 5px;
}
.win-img{
		display: none;
		position: fixed;
    width: 100%;
    height: 100%;
    background: rgba(0,0,0,0.7);
    top: 0;
    left: 0;
    z-index: 15;
}
.win-img-content{
		margin-top:20%;
		text-align: center;
}
.countdown{
		position: fixed;
    width: 20%;
    left: 40%;
    top: 35%;
    text-align: center;
    background: rgba(0,0,0,0.7);
    color: #fff;
    padding: 25px 0;
    border-radius: 10px;
    font-size:20px;
}
.countdown span{
		display: block;
		font-size:25px;
		padding-bottom: 10px;
}
